import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { signInWithEmailAndPassword } from 'firebase/auth';
import { auth } from '../firebase';
import { FcGoogle } from 'react-icons/fc';
import { FaFacebookF } from 'react-icons/fa';
import { AiFillEye, AiFillEyeInvisible } from 'react-icons/ai';

export default function Login() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [showPwd, setShowPwd] = useState(false);
  const [error, setError] = useState('');
  const navigate = useNavigate();

  const handleLogin = async e => {
    e.preventDefault();
    setError('');
    try {
      await signInWithEmailAndPassword(auth, email, password);
      navigate('/');
    } catch (err) {
      setError(err.message);
    }
  };

  return (
    <div className="min-h-screen flex flex-col lg:flex-row bg-gray-900 text-white">
      {/* Illustration */}
      <div className="hidden lg:flex lg:w-1/2 items-center justify-center p-12">
        <img
          src="https://undraw.co/api/illustrations/ef5c3bd7-c6ec-4b68-9472-5bd5e1d2f0e7"
          alt="Login illustration"
          className="w-3/4 animate-pulse"
        />
      </div>

      {/* Form */}
      <div className="flex w-full lg:w-1/2 items-center justify-center p-6">
        <div className="w-full max-w-md bg-gray-800 rounded-xl p-8 shadow-xl">
          <h1 className="text-4xl font-extrabold mb-8 text-neon text-center animate-pulse">
            Welcome to Aminoverse
          </h1>

          {/* Social Buttons */}
          <div className="flex flex-col space-y-4">
            <button className="flex items-center justify-center space-x-2 py-2 
                               border border-gray-600 rounded-lg hover:bg-gray-700 
                               transition">
              <FcGoogle /> <span>Login with Google</span>
            </button>
            <button className="flex items-center justify-center space-x-2 py-2 
                               border border-gray-600 rounded-lg hover:bg-gray-700 
                               transition">
              <FaFacebookF className="text-blue-500" /> 
              <span>Login with Facebook</span>
            </button>
          </div>

          {/* Divider */}
          <div className="flex items-center text-gray-500 my-6">
            <hr className="flex-grow border-gray-600" />
            <span className="px-3">OR</span>
            <hr className="flex-grow border-gray-600" />
          </div>

          {/* Error */}
          {error && (
            <p className="text-red-500 text-center mb-4">{error}</p>
          )}

          {/* Email/Password Form */}
          <form onSubmit={handleLogin} className="space-y-6">
            <div className="space-y-1">
              <label className="block text-gray-300">Email</label>
              <input
                type="email"
                value={email}
                onChange={e=>setEmail(e.target.value)}
                required
                className="w-full px-4 py-2 bg-gray-900 border border-gray-600 
                           rounded-lg focus:outline-none focus:border-neon transition"
                placeholder="you@example.com"
              />
            </div>
            <div className="space-y-1 relative">
              <label className="block text-gray-300">Password</label>
              <input
                type={showPwd ? 'text' : 'password'}
                value={password}
                onChange={e=>setPassword(e.target.value)}
                required
                className="w-full px-4 py-2 bg-gray-900 border border-gray-600 
                           rounded-lg focus:outline-none focus:border-neon transition"
                placeholder="â€¢â€¢â€¢â€¢â€¢â€¢â€¢â€¢"
              />
              <button
                type="button"
                onClick={()=>setShowPwd(!showPwd)}
                className="absolute right-3 top-9 text-gray-400"
              >
                {showPwd ? <AiFillEyeInvisible /> : <AiFillEye />}
              </button>
            </div>
            <div className="flex items-center justify-between text-sm text-gray-400">
              <label className="flex items-center space-x-2">
                <input type="checkbox" className="accent-neon" />
                <span>Remember me</span>
              </label>
              <Link to="/forgot-password" className="hover:underline">
                Forgot password?
              </Link>
            </div>
            <button
              type="submit"
              className="w-full py-2 bg-neon text-gray-900 font-semibold rounded-lg
                         hover:bg-indigo-500 transition shadow-md"
            >
              Log In
            </button>
          </form>

          <p className="mt-6 text-center text-gray-400">
            Donâ€™t have an account?{' '}
            <Link to="/signup" className="text-neon hover:underline">
              Register
            </Link>
          </p>
        </div>
      </div>
    </div>
  );
}
