// src/pages/Signup.jsx
import React, { useState } from 'react';
import { createUserWithEmailAndPassword } from 'firebase/auth';
import { auth } from '../firebase';
import { useNavigate, Link } from 'react-router-dom';
import { AiFillEye, AiFillEyeInvisible } from 'react-icons/ai';

export default function Signup() {
  const [email,setEmail]=useState('');
  const [password,setPassword]=useState('');
  const [showPwd,setShowPwd]=useState(false);
  const [error,setError]=useState('');
  const navigate=useNavigate();

  const handleSignup = async e => {
    e.preventDefault();
    setError('');
    try {
      await createUserWithEmailAndPassword(auth,email,password);
      navigate('/login');
    } catch(err) {
      setError(err.message);
    }
  };

  return (
    <div className="min-h-screen flex flex-col lg:flex-row bg-gray-900">
      <div className="hidden lg:flex lg:w-1/2 items-center justify-center p-12">
        <img
          src="https://undraw.co/api/illustrations/7cca6353-8626-45f6-8db5-e50fdba36810"
          alt="Signup illustration"
          className="w-3/4 animate-glow"
        />
      </div>

      <div className="flex w-full lg:w-1/2 items-center justify-center p-6">
        <div className="w-full max-w-md bg-gray-800 rounded-xl p-8 shadow-xl">
          <h1 className="text-4xl font-extrabold mb-8 text-neon text-center animate-glow">
            Create an Account
          </h1>

          {error && <p className="text-red-500 text-center mb-4">{error}</p>}

          <form onSubmit={handleSignup} className="space-y-6">
            <div className="space-y-1">
              <label className="block text-gray-300">Email</label>
              <input
                type="email"
                value={email}
                onChange={e=>setEmail(e.target.value)}
                required
                className="w-full px-4 py-2 bg-gray-900 border border-gray-600 
                           rounded-lg focus:outline-none focus:border-neon transition"
                placeholder="you@example.com"
              />
            </div>
            <div className="space-y-1 relative">
              <label className="block text-gray-300">Password</label>
              <input
                type={showPwd?'text':'password'}
                value={password}
                onChange={e=>setPassword(e.target.value)}
                required minLength={6}
                className="w-full px-4 py-2 bg-gray-900 border border-gray-600 
                           rounded-lg focus:outline-none focus:border-neon transition"
                placeholder="â€¢â€¢â€¢â€¢â€¢â€¢â€¢â€¢"
              />
              <button
                type="button"
                onClick={()=>setShowPwd(!showPwd)}
                className="absolute right-3 top-9 text-gray-400"
              >
                {showPwd ? <AiFillEyeInvisible/> : <AiFillEye/>}
              </button>
            </div>
            <button
              type="submit"
              className="w-full py-2 bg-neon text-gray-900 font-semibold rounded-lg
                         hover:bg-indigo-500 transition shadow-md"
            >
              Sign Up
            </button>
          </form>

          <p className="mt-6 text-center text-gray-400">
            Already have an account?{' '}
            <Link to="/login" className="text-neon hover:underline">
              Log In
            </Link>
          </p>
        </div>
      </div>
    </div>
  );
}
