// src/pages/Home.jsx
import React, { useState } from 'react';
import Navbar from '../components/Navbar';
import SearchBar from '../components/SearchBar';
import ChatSidebar from '../components/ChatSidebar';
import ChatWindow from '../components/ChatWindow';
import { fetchUniProtData } from '../api/uniprot';
import { saveMessage } from '../api/saveMessageClient';

const Home = () => {
  const [chatHistory, setChatHistory] = useState([]);   // stores { prompt, response } objects
  const [messages, setMessages] = useState([]);         // for rendering in ChatWindow
  const [proteinInfo, setProteinInfo] = useState(null);

  const handleSearch = async (query) => {
    // 1ï¸âƒ£ record user prompt
    setMessages(ms => [...ms, { sender: 'user', text: query }]);
    setChatHistory(h => [...h, { prompt: query, response: null }]);

    try {
      const data = await fetchUniProtData(query);
      const entry = data.results?.[0] || null;
      setProteinInfo(entry);

      const funcComment = entry?.comments
        ?.find(c => c.commentType === 'FUNCTION')?.texts?.[0]?.value
        || 'No function data available.';

      const botResponse = `Function: ${funcComment}`;

      // 2ï¸âƒ£ record bot reply
      setMessages(ms => [...ms, { sender: 'bot', text: botResponse }]);
      setChatHistory(h => {
        const copy = [...h];
        copy[copy.length - 1].response = botResponse;
        return copy;
      });

      // 3ï¸âƒ£ persist to your MongoDB via Cloud Function
      await saveMessage(query, botResponse);
    } catch (err) {
      console.error("Search error:", err);
    }
  };

  const handleSend = async (userInput) => {
    // record user prompt
    setMessages(ms => [...ms, { sender: 'user', text: userInput }]);
    setChatHistory(h => [...h, { prompt: userInput, response: null }]);

    // placeholder bot logic
    const botResponse = "ðŸ¤– Sorry, I don't have an answer for that yet.";

    // record bot reply
    setMessages(ms => [...ms, { sender: 'bot', text: botResponse }]);
    setChatHistory(h => {
      const copy = [...h];
      copy[copy.length - 1].response = botResponse;
      return copy;
    });

    // persist
    await saveMessage(userInput, botResponse);
  };

  return (
    <div className="min-h-screen bg-black text-white font-sans">
      <Navbar />
      <div className="flex">
        {/* Sidebar for history */}
        <ChatSidebar
          prompts={chatHistory.map(h => h.prompt + (h.response ? ` â†’ ${h.response}` : ''))}
          onSelect={(text) => {
            // when user clicks a history item, re-send it
            handleSearch(text);
          }}
        />

        {/* Main Content */}
        <main className="flex-1 p-6">
          {/* Top search bar */}
          <SearchBar onSearch={handleSearch} />

          {/* Hero / protein details */}
          {proteinInfo && (
            <div className="bg-gray-800 text-white rounded-lg p-6 my-6 animate-fade-in">
              <h2 className="text-2xl font-bold mb-2 text-neon">
                {proteinInfo.proteinDescription?.recommendedName?.fullName?.value}
              </h2>
              <p><strong>Accession:</strong> {proteinInfo.primaryAccession}</p>
              <p><strong>Gene Names:</strong> {proteinInfo.genes?.[0]?.geneName?.value || 'â€”'}</p>
              <p><strong>Organism:</strong> {proteinInfo.organism?.scientificName}</p>
              <p><strong>Length:</strong> {proteinInfo.sequence?.length} aa</p>
              <div className="mt-4">
                <strong>Function:</strong> {
                  proteinInfo.comments
                    ?.filter(c => c.commentType === 'FUNCTION')
                    .map(c => c.texts?.[0]?.value).join('; ') || 'â€”'
                }
              </div>
            </div>
          )}

          {/* Chat prompt area */}
          <ChatWindow messages={messages} onSend={handleSend} />
        </main>
      </div>
    </div>
  );
};

export default Home;
