// src/components/ChatWindow.jsx
import React, { useState } from 'react';

const ChatWindow = ({ messages, onSend }) => {
  const [userInput, setUserInput] = useState('');

  const handleInputChange = (e) => {
    setUserInput(e.target.value);
  };

  const handleSend = () => {
    if (userInput.trim() !== '') {
      onSend(userInput);
      setUserInput('');
    }
  };

  return (
    <div className="flex-1 bg-gray-800 p-6 rounded-lg">
      <div className="h-96 overflow-y-auto space-y-4 mb-4">
        {/* Render each message in the chat history */}
        {messages.map((message, index) => (
          <div key={index} className={`chat-message ${message.sender === 'user' ? 'text-right' : 'text-left'}`}>
            <div
              className={`inline-block p-2 mb-2 rounded-lg max-w-xs ${
                message.sender === 'user' ? 'bg-neon text-black' : 'bg-gray-600 text-white'
              }`}
            >
              {message.text}
            </div>
          </div>
        ))}
      </div>

      {/* User Input Area */}
      <div className="flex items-center">
        <input
          type="text"
          value={userInput}
          onChange={handleInputChange}
          onKeyDown={(e) => e.key === 'Enter' && handleSend()}
          className="w-full p-2 bg-gray-700 text-white rounded-l-lg focus:outline-none"
          placeholder="Type your message..."
        />
        <button
          onClick={handleSend}
          className="p-2 bg-neon text-black rounded-r-lg hover:bg-neon-dark focus:outline-none"
        >
          Send
        </button>
      </div>
    </div>
  );
};

export default ChatWindow;
