import React from 'react';

const SearchBar = ({ onSearch }) => {
  return (
    <div className="flex justify-center mt-6">
      <input
        type="text"
        placeholder="Search..."
        onChange={(e) => onSearch(e.target.value)}
        className="w-1/2 px-4 py-2 rounded-full bg-black border-2 border-neon text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-neon transition duration-300"
      />
    </div>
  );
};

export default SearchBar;
